.lootbox {
  display: grid;
  height: 100vh;
  width: 100vw;
  grid-template-rows: 18vh 1fr 18vh;
}

.lootbox__line {
  position: relative;
  height: 18vh;
  width: 100%;
  border-bottom: 2px solid;
  border-image-source: linear-gradient(to right, #29B8FF, #7E28F9);
  border-image-slice: 1;
  border-image-outset: 0;
  border-image-repeat: stretch;
}

.lootbox__line:last-child {
  border-bottom: 0;
  border-top: 2px solid;
}

.lootbox__line::after {
  content: ' ';
  position: absolute;
  left: 0;
  bottom: -2px;
  z-index: 2;
  width: 100%;
  height: 2px;
  opacity: 0;
  transition: 8s opacity ease-in-out;
}

.lootbox__line:last-child::after {
  top: -2px;
}

.lootbox__line_end::after {
  opacity: 1;
  box-shadow: 0px 0px 4px #7E28F9,
              0px 0px 10px #7E28F9,
              0px 0px 20px #7E28F9,
              0px 0px 40px #7E28F9,
              0px 0px 60px #7E28F9;
}