.lootbox__openWindowWrapper {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.lootbox__openWindow {
  height: 75vh;
  width: calc(48vh + 122px);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 61px 70px 61px;
  backdrop-filter: blur(10px);
}

.lootbox__openWindowImg {
  height: 48vh;
  margin-bottom: 22px;
}