.actions {
  position: absolute;
  top: 0;
  transform: translate(0, -50%);
  display: flex;
  align-items: stretch;
  justify-content: center;
  gap: 5vw;
  padding: 0 16.6vw 0;
  z-index: 1;
  width: 100vw;
}

.actions__button {
  width: 31.25vw;
}

.actions__button_openMore:first-child {
  width: max-content;
}

.actions__button_openMore:last-child {
  width: max-content;
}