.resultItem {
  margin-top: 10vh;
  width: 100vw;
  margin-bottom: -12vh;
}

.resultItem__content {
  display: flex;
  align-items: center;
  gap: 3vw;
  width: 75vw;
  margin: 0 8.3vw 0 16.6vw;
}

.resultItem__image {
  width: 25vw;
  height: 42vh;
  aspect-ratio: 4.8 / 4.6;
  object-fit: contain;
}

.resultItem__description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.resultItem__type {
  color: #B96DFF;
  font-weight: 400;
  font-size: 4.4vmin;
  margin: 0 0 2.7vh;
}

.resultItem__char {
  font-size: 2.7vmin;
  line-height: 1.3;
  color: #FFFFFF;
  text-align: left;
  margin: 0 0 2.7vh;
}

.resultItem__char:last-child {
  margin-bottom: 0;
}

.resultItem__label {
  font-weight: 400;
  font-size: 2.7vmin;
  color: #0F49E6;
  margin-bottom: 2.2vh;
}
