.modal__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.7);
  backdrop-filter: blur(10);
  z-index: 1000;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  padding: 2em;
  max-width: 50vw;
  max-height: 50vh;
}

.modal__inner {
  padding: 2em;
  background: #0F1337;
  backdrop-filter: blur(20);
}