.lootbox__carouselWrapper {
  position: relative;
  width: 100vw;
  overflow: hidden;
  height: 100%;
  display: flex;
  align-items: center;
}

.lootbox__carousel {
  position: relative;
  display: flex;
  align-items: center;
  height: calc(62.5vh - 30px);
  left: 0;

  transition: 2.5s left linear;
}

.lootbox__carousel_notStarted {
  opacity: 0.4;
  animation: inherit;
}

.lootbox__carousel_endPosition_end {
  transition: 4s left ease-out;
}

.lootbox__carouselItem {
  min-width: 22.5vw;
  width: 22.5vw;
  padding: 0 0.75vw;
}

.lootbox__carouselMask {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  width: 100%;
  height: 24vw;
  display: flex;
  align-items: center;
  z-index: 2;
}

.lootbox__carouselTarget {
  width: 24vw;
  height: 24vw;
  transition: 8s;
}

.lootbox__carouselTarget_end {
  box-shadow: 0px 0px 4px #7E28F9,
              0px 0px 10px #7E28F9,
              0px 0px 20px #7E28F9;
}

.lootbox__carouselBlur {
  width: calc(50% - 12vw);
  backdrop-filter: blur(10px);
  height: 24vw;
  transition: 8s;
  background: transparent;
  opacity: 1;
}

.lootbox__carouselBlur_end {
  backdrop-filter: inherit;
  background: #0F1337;
  opacity: 0.6;
}

.lootbox__carouselNeon {
  position: fixed;
  top: 50%;
  left: 50%;
  height: 112vh;
  width: 112vh;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: 8s;
}

.lootbox__carouselNeon_end {
  opacity: 1;
  background: radial-gradient(50% 50% at 50% 50%, rgba(126, 40, 249, 0.4) 0%, rgba(126, 40, 249, 0) 100%);
}