.button {
  padding: 2.7vh 3.7vw;
  color: #fff;
  box-shadow: 0 0 40px #0F1337;
  font-size: 2.7vmin;
  width: fit-content;
  cursor: pointer;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button_primary {
  background: #0F49E6;
}

.button_secondary {
  background: #7E28F9;
}